.home-tabs {
  .tabs {
    &__inner {
      @include flex-ss;
    }

    &__nav {
      flex-direction: column;
      margin-right: rem(100);
      border-right: 1px solid rgba(0, 0, 0, 0.1);
      padding-right: rem(100);
      width: 100%;
      max-width: 530px;
    }

    &__nav-item {
      &:not(:last-of-type) {
        margin-bottom: 20px;
      }
    }

    &__nav-btn {
      position: relative;
      display: block;
      margin: 0;
      border-radius: calc(var(--radius-main) * 2);
      padding: rem(34) rem(30) rem(34) 80px;
      width: 100%;
      font-weight: 500;
      font-size: var(--size-h3);
      text-align: left;
      transition: color 0.15s ease, background-color 0.15s ease;

      &::before {
        content: "";
        position: absolute;
        left: rem(25);
        top: 50%;
        mask-size: 10px;
        border: 1px solid rgba(0, 0, 0, 0.1);
        border-radius: var(--radius-main);
        width: 40px;
        height: 40px;
        transform: translateY(-50%);
      }

      &::after {
        content: "";
        position: absolute;
        left: rem(25);
        top: 50%;
        mask-position: center;
        mask-repeat: no-repeat;
        border: 1px solid rgba(0, 0, 0, 0.1);
        border-radius: var(--radius-main);
        width: 40px;
        height: 40px;
        background-color: var(--color-main);
        transform: translateY(-50%);
      }

      &:focus {
        color: var(--color-dark-800);
        background-color: var(--color-background);
      }

      &:hover {
        color: var(--color-main);
      }

      &--layers {
        &::after {
          mask-image: url("../img/sprite.svg#layers");
          mask-size: 20px;
        }
      }

      &--socialize {
        &::after {
          mask-image: url("../img/sprite.svg#column-chart");
          mask-size: 20px;
        }
      }

      &--earn {
        &::after {
          mask-image: url("../img/sprite.svg#up-down");
          mask-size: 20px;
        }
      }

      &--learn {
        &::after {
          mask-image: url("../img/sprite.svg#cases");
          mask-size: 20px;
        }
      }

      &--tools {
        &::after {
          mask-image: url("../img/sprite.svg#column-window");
          mask-size: 20px;
        }
      }

      &--brand {
        &::after {
          mask-image: url("../img/sprite.svg#bell");
          mask-size: 20px auto;
        }
      }

      &--active {
        background-color: var(--color-background);

        &::before {
          background-color: var(--color-main);
        }

        &::after {
          background-color: var(--color-white);
        }

        &:hover {
          color: var(--color-dark-800);
        }
      }
    }

    &__title {
      margin-bottom: 20px;
      font-weight: 700;
    }

    &__content {
      width: 100%;
    }

    &__image {
      margin-bottom: rem(40);
      width: 100%;
      max-width: 525px;
    }

    &__panel {
      display: none;
      width: 100%;
      max-width: 650px;
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.4s linear, visibility 0.4s linear;

      &--active {
        display: block;
        opacity: 1;
        visibility: visible;
      }
    }

    .content {
      margin-bottom: rem(50);
      font-size: var(--size);
    }
  }
}
